html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  background: var(--background-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 1.75rem;
}
@media (min-width: 700px) {
  h1 {
    font-size: 2.25rem;
  }
}
@media (min-width: 900px) {
  h1 {
    font-size: 2.625rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media (min-width: 700px) {
  h2 {
    font-size: 1.8rem;
  }
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --background-color: #010b26;
  --text-color: #fff;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, auto);
  gap: 2.5em;
  width: min(100% - 2.5em, 1000px);
  margin-inline: auto;
}
@media (min-width: 700px) {
  .grid {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
  }
}
.grid__main, .grid__sidebar {
  padding: 2.5em;
}
.grid__main {
  background: #434384;
}
@media (max-width: 899.98px) {
  .grid__main {
    background: #a21717;
  }
}
@media (max-width: 899.98px) {
  .grid__sidebar {
    text-align: left;
  }
}
@media (max-width: 699.98px) {
  .grid__sidebar {
    text-align: center;
  }
}
.grid__widget, .grid__widget--green {
  background: #b86ca2;
  padding: 1em;
}
.grid__widget--green {
  background: #b3c1e2;
  color: #000;
}