@use "../util" as u;

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 1.75rem;

  @include u.breakpoint(medium) {
    font-size: 2.25rem;
  }

  @include u.breakpoint(large) {
    font-size: 2.625rem;
  }
}

h2 {
  font-size: 1.5rem;

  @include u.breakpoint(medium) {
    font-size: 1.8rem;
  }
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}
